import React, { useState, useEffect } from 'react';
import {Link} from 'react-router-dom'
import SearchBar from './SearchBar'
import Catagories from './Catagories'
import SideBarTitle from './SideBarTitle'
import BlogNewsletter from './BlogNewsletter'
import BlogSubContent from './BlogSubContent';

function BlogContent({ blogs_data }) {
  const [blogs, setBlogs] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [blogsPerPage] = useState(5); // Number of blogs to display per page

  useEffect(() => {
    // Fetch blogs from your data source and update the state
    // Replace this with your actual API call or data retrieval logic
    // For demonstration purposes, we'll use the dummy data
    setBlogs(blogs_data);
  }, []);

  var indexOfLastBlog = currentPage * blogsPerPage;
  var indexOfFirstBlog = indexOfLastBlog - blogsPerPage;
  

  const paginate = (pageNumber) => {
    setCurrentPage(pageNumber);

  };

  const [selectedCategory, setSelectedCategory] = useState('all'); // Initial category: 'all'
  const handleCategoryClick = (category) => {
    setSelectedCategory(category);
    paginate(1)
  };

  
  const filteredBlogs = selectedCategory === 'all'
  ? blogs_data // Show all blogs if 'all' category is selected
  : blogs_data.filter((blog) => blog.category === selectedCategory);
  const currentBlogs = filteredBlogs.slice(indexOfFirstBlog, indexOfLastBlog);
  

  return (
      <section className="standard-blog-area">
        <div className="container">
          <div className="row justify-content-center">
            <div className="col-lg-8">
            {currentBlogs.map((blog) => (
              
                  <div>
                    <BlogSubContent blog={blog} />
                  </div>
            ))}
                <div className="shop-page-meta">
                  <div className="shop-showing-result">
                    {/* <p>Total Items 1-12 of 13</p> */}
                  </div>
                  {/* <div className="shop-show-list">
                    <form>
                      <label htmlFor="bottomShow">Show</label>
                      <select id="bottomShow" className="selected">
                        <option value>08</option>
                        <option value>12</option>
                        <option value>16</option>
                        <option value>18</option>
                        <option value>20</option>
                      </select>
                    </form>
                  </div> */}
                  <ul>
                    {/* {Array.from({ length: Math.ceil(blogs.length / blogsPerPage) }, (_, index) => (
                      <button key={index} onClick={() => paginate(index + 1)}>
                        {index + 1}
                      </button>
                    ))} */}
                    </ul>
                    
                  <div className="shop-pagination">
                    <ul>
                       {Array.from({ length: Math.ceil(blogs.length / blogsPerPage) }, (_, index) => (
                      <button  key={index} onClick={() => paginate(index + 1)}>{index+1}</button>
                    ))}
                    </ul>
                  </div>
                </div>
            </div>
                  {/* Pagination */}
      <div>

      </div>
            <div className="col-lg-4 col-md-8">
              {/* <Catagories/> */}
              <div className="widget">
                  <h4 className="sidebar-title">Categories</h4>
                  <div className="sidebar-cat-list">
                    <ul>
                    <li><a href="#" onClick={() => handleCategoryClick('mensuration')}>Mensuration <i className="fas fa-angle-double-right" /></a></li>
                    <li><a href="#" onClick={() => handleCategoryClick('crypto')}>Crypto <i className="fas fa-angle-double-right" /></a></li>
                    </ul>
                  </div>
                </div>
              <br></br>
            <BlogNewsletter/>
            </div>
          </div>
        </div>
      </section>
  )
}

export default BlogContent;